import React, { useState, useCallback } from "react";

function Input({ baby }) {
  const [gender, setGender] = useState("");
  const onChange = useCallback((e) => {
    setGender(e.target.value);
  }, []);
  const onSave = useCallback(() => {
    baby.set({
      gender,
    });
  }, [baby, gender]);

  return (
    <div>
      <label>
        <input
          type="radio"
          value="boy"
          checked={gender === "boy"}
          onChange={onChange}
        />
        Boy
      </label>
      <label>
        <input
          type="radio"
          value="girl"
          checked={gender === "girl"}
          onChange={onChange}
        />
        Girl
      </label>
      <button type="submit" onClick={onSave}>
        Save
      </button>
    </div>
  );
}

export default Input;
